import { Component, Prop, h, Host } from '@stencil/core';
import { cva } from 'class-variance-authority';

const variants = cva(
    'relative flex w-full cursor-pointer items-center gap-8 overflow-hidden truncate rounded-md p-8 text-left text-body font-normal transition-[background-color,color,box-shadow] duration-150 ease-in-out focus:outline-0 focus-visible:ring-2 focus-visible:ring-inset focus-visible:ring-active-foreground/40',
    {
        variants: {
            disabled: {
                true: 'pointer-events-none opacity-30 grayscale-[1]',
                false: null,
            },
            active: {
                true: 'bg-active-foreground/15 text-active',
                false: 'hover:bg-surface-1',
            },
        },
    },
);

@Component({
    tag: 'atui-menu-item',
})
export class AtuiMenuitemComponent {
    /**
     * Label to be displayed for the menu item
     */
    @Prop() label?: string;

    /**
     * Icon to be displayed with the label
     */
    @Prop() icon?: string;

    /**
     * Will change the styling of the menuitem when set
     */
    @Prop() is_active?: boolean = false;

    /**
     * Disables user interaction with the menu-item and updates visual style to appear inactive
     */
    @Prop()
    disabled: boolean = false;

    render() {
        const classname = variants({
            active: this.is_active,
            disabled: this.disabled,
        });

        return (
            <Host
                role="menuitem"
                tabindex="0"
                class={classname}
                onClick={(e: Event) => {
                    if (this.disabled) {
                        e.preventDefault();
                        e.stopPropagation();
                    }
                }}
            >
                {this.icon && (
                    <span
                        data-name="menu-item-icon"
                        class="material-icons flex items-center justify-center text-icon-lg"
                    >
                        {this.icon}
                    </span>
                )}
                {this.label && (
                    <span
                        data-name="menu-item-label"
                        class="truncate whitespace-nowrap group-data-[state=collapsed]/sidebar-wrapper:hidden"
                    >
                        {this.label}
                    </span>
                )}
                <slot />
            </Host>
        );
    }
}
